*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a{
  color: #333;
  text-decoration: none;
}

.container{
  padding: 0 1rem;
}

.btn{
  display: inline-block;
  border: none;
  background: #0000F4;
  color: #FFF;
  padding: 7px 20px;
  cursor: pointer;
}

.btn:hover{
  background: #0099F4;
}